.update {
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background-color: rgba(0, 0, 0, 0.7);
    z-index: 999;
    display: flex;
    align-items: center;
    justify-content: center;
    &__form {
      width: 700px;
      background-color: #f9f8f4;;
      padding: 15px;
      display: flex;
      flex-direction: column;
      position: relative;
      border-radius: 10px;
    }
    &__icon {
      position: absolute;
      top: 5px;
      right: 5px;
      color: var(--red-color);
      cursor: pointer;
      font-size: 30px;
    }
    &__posttitle {
      margin-bottom: 10px;
      font-size: 24px;
      color: var(--blue-color);
      text-align: center;
    }
    &__input {
      width: 100%;
      border-radius: 10px;
      font-size: 21px;
      border: 1px solid #111111;
      padding: 10px;
      margin: 10px 0;
    }
    &__btn {
      width: 100%;
      cursor: pointer;
      text-align: center;
      background-color: var(--blue-color);
      border: none;
      font-size: 21px;
      font-weight: 500;
      color: #ffffff;
      border-radius: 10px;
      padding: 10px;
      margin-top: 15px;
    }
  }
  
  // MEDIA QUERIES
  
  @media screen and (max-width: 820px) {
    .update {
      &__form {
        width: 90%;
      }
    }
  }
  