.verifyemail {
  height: calc(100vh - 130px);
  width: 100%;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  &__icon {
    font-size: 80px;
    color: var(--blue-color);
  }
  &__title {
    font-size: 32px;
    margin-bottom: 20px;
    color: var(--blue-color);
  }
  &__link {
    font-size: 24px;
    font-weight: 600;
    background-color: #111111;
    padding: 10px;
    border-radius: 10px;
    color: var(--yellow-color);
  }
  &__notfound{
    font-size: 40px;
    color: var(--red-color);
  }
}
